import React, { Component } from "react";

import Navbar from "../components/Navbar/Navbar.jsx";
import Jumbotron from "../components/Jumbotron/Jumbotron.jsx";
import "../images/avatar.jpg";
import "./Home.css";

class Home extends Component {
  render() {
    return (
      <div>
        <Navbar />
        <Jumbotron title="Kim jestem ?" />
        <div className="container">
          <div className="row">
            <div className="col-12 col-sm-7">
              <h2>Dzie dobrry!</h2>
              <p>
                Mam na imi Aneta, studiuj Informatyk na Szkole Gwnej Gospodarstwa Wiejskiego w Warszawie. 
                jestem samotn matk 6-letniej kocicy Muni. W wolnej chwili uzupeniam wiedze w zakresie finansw i najnowszej technologii, oraz czytam ksiki i ogldam seriale na Netflixie. 

              </p>
              <h3>A tu kilka moich umiejtnoci:</h3>
              <ul>
                <li>wietna komunikacja w zespole </li>
                <li>
                  precyzyjno 
                </li>
                <li>
                  kreatywno 
                </li>
                <li>
                  umiejtno pracy pod presj
                </li>
                <li>
                  umiejtno znajdywania najprostszych rozwiza 
                </li>
              </ul>
            </div>
            <div className="col-sm-1"></div>
            <div className="col-12 col-sm-4">
              <img
                alt=""
                className="profile"
                src={require("../images/avatar.jpg")}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Home;



